body {
  font-family: "Fira Sans", sans-serif;
  padding: 0;
  margin: 10px;
  overflow-x: hidden;
  background-color: rgb(0, 0, 0);
  color: white;
}
.user-profile {
  color: black;
  width: 85%;
  height: 10rem;
  background-color: rgb(116, 70, 70);
  margin: 0.5em;
  display: flex;
  margin-left: 7.5%;
  margin-right: 7.5%;
  justify-content: center;
  border-radius: 80px 0 0 80px;
  /*border: solid 1px;*/
}
.username {
  color: rgba(255, 255, 255, 0.87);
}
.thumbnail {
  background-color: blanchedalmond;
  width: 160px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 5em;
  border-radius: 50%;
}
.info {
  padding-left: 10px;
  margin-right: 10px;
  display: flex;
  flex-direction: column;
  width: 87.1%;
  font-size: 20px;
  /*border: solid 1px;*/
}
.name {
  justify-content: flex-end;
  color: blanchedalmond;
  font-size: 27px;
  /*border: solid 1px;*/
  display: flex;
}
li {
  color: white;
  padding: 5px;
  margin: 5px;
  border: solid 0.3px rgb(116, 70, 70);
  border-radius: 10px;
}
li:hover {
  border: solid 1px blanchedalmond;
}
.current-page-item {
  background: blanchedalmond;
  color: black;
  border: solid 1px blanchedalmond;
}
.current-page-item:hover {
  border: solid 0.3px rgb(116, 70, 70);
}
ul {
  list-style: none;
  display: flex;
  margin-right: 7.5%;
  float: right;
}
a {
  color: inherit;
  text-decoration: inherit;
  cursor: pointer;
}
input {
  width: 20%;
  border: 0.2px solid rgb(116, 70, 70);
  border-radius: 4px;
  height: 25px;
}
/* Media screen size */
@media screen and (max-width: 768px) {
  input {
    width: 260px;
  }
}
@media screen and (max-width: 600px) {
  .user-profile {
    height: 18rem;
    flex-direction: column;
    border-radius: 0px 20px 0 20px;
  }
  .name {
    width: 100%;
    align-content: center;
    text-align: center;
    padding: 3%;
    justify-content: center;
  }
  .info {
    padding-left: 0px;
    margin-right: 0px;
    padding: 2%;
    display: flex;
    flex-direction: column;
    width: 95%;
    font-size: 20px;
    /*border: solid 1px;*/
  }
  .thumbnail {
    height: 15%;
    width: 100%;
    border-radius: 0;
    font-size: 3em;
    text-align: center;
    padding-right: 0;
    margin-right: 0;
    padding-left: 0;
    margin-left: 0;
  }
}
